import React from "react";

import Layout from "components/layout";
import Hero from "components/agency/partners/hero";
import ValueProps from "components/agency/partners/valueProps";
import Testimonials from "components/agency/partners/testimonials";
import Comparison from "components/agency/partners/comparison";
import SignUp from "components/agency/partners/signup";
import { branch, compose, renderComponent, withStateHandlers } from "recompose";
import ThankYou from "components/agency/partners/thankyou";

const _Content = ({ setHasSignedUp }) => (
  <React.Fragment>
    <Hero />
    <ValueProps />
    <Testimonials />
    <SignUp setHasSignedUp={setHasSignedUp} />
    <Comparison />
  </React.Fragment>
);

const Agency = ({ location }) => (
  <Layout
    location={location}
    whiteGutterBackground
    whiteContentBackground
    title="Become an agency partner - Brex"
    description="Become a Brex Partner today to unlock valuable budget and tools for your clients while growing your business. Join today!"
    removeToast
  >
    <Content />
  </Layout>
);

const Content = compose(
  withStateHandlers(
    { hasSignedUp: false },
    { setHasSignedUp: () => (value) => ({ hasSignedUp: value }) },
  ),
  branch(
    ({ hasSignedUp }) => !!hasSignedUp,
    renderComponent(({ setHasSignedUp }) => (
      <ThankYou setHasSignedUp={setHasSignedUp} />
    )),
  ),
)(_Content);

export default Agency;
