import React from "react";
import { isEmpty } from "lodash";
import { withFormik } from "formik";
import * as Yup from "yup";
import { compose } from "recompose";

import { Row, Column } from "components/Grid.tsx";
import withGETFormSubmission from "hocs/withGETFormSubmission";
import EnhancedInput from "components/form/EnhancedInput";
import EnhancedSelect from "components/form/EnhancedSelect";

import HashLinkDestination from "components/foundational/HashLinkDestination";
import { signupFeatures } from "../messages";

import {
  Title,
  Wrapper,
  Form,
  Button,
  FeatureWrapper,
  FeatureLogo,
  FeatureTitle,
  FeatureDescription,
  FeaturesContainer,
  ErrorMessage,
} from "./elements";

const shortAnswerFields = [
  {
    id: "entry.608606209",
    name: "email",
    type: "text",
    placeholder: "Work email",
  },
  {
    id: "entry.756472087",
    name: "phoneNumber",
    type: "text",
    placeholder: "Phone number",
  },
  {
    id: "entry.1551237457",
    name: "companyName",
    type: "text",
    placeholder: "Company name",
  },
  {
    id: "entry.423569659",
    name: "companyWebsite",
    type: "text",
    placeholder: "Company website",
  },
];

const selectFields = [
  {
    description: "Number of clients served",
    value: "",
    selected: "selected",
    disabled: true,
  },
  {
    description: "20 or less",
    value: "2_20",
  },
  {
    description: "21-50",
    value: "21_50",
  },
  {
    description: "50 +",
    value: "51_or_more",
  },
];

const longAnswerField = {
  id: "entry.1186813426",
  name: "hearAbout",
  type: "text",
  placeholder: "How did you hear about us?",
};

const googleForm =
  "https://docs.google.com/forms/d/1i7yNRdOpusSc2dX_YnArhddebyhSysJHzgWwTkfcS1o/formResponse";

const Feature = ({ feature }) => (
  <FeatureWrapper>
    <FeatureLogo src={feature.image} />
    <FeatureTitle>{feature.title}</FeatureTitle>
    <FeatureDescription>{feature.description}</FeatureDescription>
  </FeatureWrapper>
);

const getInputForField = ({
  field: { type, name, id, placeholder },
  handleBlur,
  handleChange,
  values,
  errors,
  touched,
}) => (
  <EnhancedInput
    type={type}
    name={name}
    id={id}
    placeholder={placeholder}
    onBlur={handleBlur}
    onChange={handleChange}
    value={values[name]}
    error={errors[name]}
    touched={touched[name]}
    key={id}
  />
);

const _SignUpForm = ({
  values,
  errors,
  touched,
  handleChange,
  handleBlur,
  isSubmitting,
  submitForm,
  setHasSignedUp,
  successfulSubmission,
  failedSubmission,
}) => {
  if (successfulSubmission) {
    setHasSignedUp(true);
  }

  return (
    <React.Fragment>
      <Form onSubmit={submitForm}>
        {shortAnswerFields.map((field) =>
          getInputForField({
            field,
            handleBlur,
            handleChange,
            values,
            errors,
            touched,
          }),
        )}
        <EnhancedSelect
          name="numberOfClients"
          id="entry.1198258526"
          title="Number of clients served"
          handleChange={handleChange}
          options={selectFields}
          error={errors.numberOfClients}
          touched={touched.numberOfClients}
        />
        {getInputForField({
          field: longAnswerField,
          handleBlur,
          handleChange,
          values,
          errors,
          touched,
        })}
        <Button
          type="submit"
          disabled={isEmpty(touched) || !isEmpty(errors) || isSubmitting}
        >
          BECOME A PARTNER
        </Button>
      </Form>
      {failedSubmission && (
        <ErrorMessage>
          There has been an error submitting the form. Please try again.
        </ErrorMessage>
      )}
    </React.Fragment>
  );
};

const formSchema = Yup.object().shape({
  phoneNumber: Yup.string(),
  companyWebsite: Yup.string().required("Required"),
  email: Yup.string()
    .email("Email must be valid")
    .required("Required"),
  companyName: Yup.string().required("Required"),
  numberOfClients: Yup.string().required("Required"),
});

const formConfig = {
  mapPropsToValues: () => ({
    phoneNumber: "",
    companyWebsite: "",
    email: "",
    companyName: "",
    numberOfClients: "",
  }),
  validationSchema: formSchema,
  validateOnBlur: true,
  validateOnChange: true,
};
const SignUpForm = compose(
  withFormik(formConfig),
  withGETFormSubmission({ formLink: googleForm }),
)(_SignUpForm);

const SignUp = ({ setHasSignedUp, headerOffset }) => (
  <HashLinkDestination id="signup" headerOffset={headerOffset}>
    <Wrapper>
      <Row padding={30} gutterWidth={26}>
        <Column span={1} spanSmall={0} />
        <Column span={10} spanSmall={12}>
          <Title>
            Give your clients the best corporate card for modern businesses
          </Title>
        </Column>
        <Column span={1} spanSmall={0} />
      </Row>
      <Row padding={30} gutterWidth={26}>
        <Column span={1} spanSmall={0} />
        <Column span={5} spanMedium={10} spanSmall={12}>
          <SignUpForm setHasSignedUp={setHasSignedUp} />
        </Column>
        <Column span={0} spanMedium={1} spanSmall={0} />
        <Column span={1} spanSmall={0} />
        <Column span={5} spanMedium={10} spanSmall={12}>
          <FeaturesContainer>
            {signupFeatures.map((feature, index) => (
              <Feature feature={feature} key={`feature-${index + 1}`} />
            ))}
          </FeaturesContainer>
        </Column>
        <Column span={0} spanMedium={1} spanSmall={0} />
      </Row>
    </Wrapper>
  </HashLinkDestination>
);

export default SignUp;
