import { compose, withHandlers, withStateHandlers } from "recompose";
import { reduce } from "ramda";

// This HOC will get the input of your form, append it to the formLink in URL
// parameter format, and make a GET request. This is the way we submit to Google
// forms from custom styled forms on the website.
//
// Make sure to set the id of the form inputs to the desired parameter name.
//
// NOTE: Currently only works for text-based inputs. Change the if statement
// within the reduce function to allow for other inputs.

const withGETFormSubmission = ({ formLink }) =>
  compose(
    withStateHandlers(
      {
        successfulSubmission: false,
        failedSubmission: false,
      },
      {
        setSubmissionSuccess: () => (wasSuccessful) => ({
          successfulSubmission: wasSuccessful,
          failedSubmission: !wasSuccessful,
        }),
      },
    ),
    withHandlers({
      submitForm: ({ setSubmissionSuccess }) => (e) => {
        e.preventDefault();
        const formWithParameters = reduce(
          (acc, curr) => {
            // skips any input that isn't text.
            if (curr.type === "submit") {
              return acc;
            }
            return `${acc}${curr.id}=${curr.value}&`;
          },
          `${formLink}?`,
          e.target.elements,
        );

        fetch(formWithParameters, {
          method: "GET",
          mode: "no-cors",
        }).then(
          () => setSubmissionSuccess(true),
          () => setSubmissionSuccess(false),
        );
      },
    }),
  );

export default withGETFormSubmission;
